"use client";

import { useState, useMemo, useEffect } from "react";
import { useRouter } from "next/navigation";
import { supabase } from "@/integrations/supabase/client";
import { User, Session } from "@supabase/supabase-js";
import { Meme, Category } from "@/types/meme";
import { mockMemes } from "@/data/mockMemes";
import { SearchBar } from "@/components/SearchBar";
import { CategoryFilter } from "@/components/CategoryFilter";
import { MemeCard } from "@/components/MemeCard";
import { MemeModal } from "@/components/MemeModal";
import { UploadMemeDialog } from "@/components/UploadMemeDialog";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Image, Upload, LogOut, LogIn, Shield } from "lucide-react";
import { toast } from "sonner";
import { useUserRole } from "@/hooks/useUserRole";

export default function HomePage() {
  const router = useRouter();
  const [user, setUser] = useState<User | null>(null);
  const [session, setSession] = useState<Session | null>(null);
  const [memes, setMemes] = useState<Meme[]>([]);
  const [searchQuery, setSearchQuery] = useState("");
  const [selectedCategory, setSelectedCategory] = useState<Category>("전체");
  const [selectedMeme, setSelectedMeme] = useState<Meme | null>(null);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isUploadOpen, setIsUploadOpen] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const { isAdmin, role } = useUserRole(user);

  // Auth state management
  useEffect(() => {
    // Set up auth state listener FIRST
    const { data: { subscription } } = supabase.auth.onAuthStateChange(
      (event, session) => {
        setSession(session);
        setUser(session?.user ?? null);
      }
    );

    // THEN check for existing session
    supabase.auth.getSession().then(({ data: { session } }) => {
      setSession(session);
      setUser(session?.user ?? null);
    });

    return () => subscription.unsubscribe();
  }, []);

  // Fetch memes from database
  const fetchMemes = async () => {
    setIsLoading(true);
    try {
      // Supabase 환경 변수 확인
      const supabaseUrl = process.env.NEXT_PUBLIC_SUPABASE_URL;
      const supabaseKey = process.env.NEXT_PUBLIC_SUPABASE_PUBLISHABLE_KEY;
      
      if (!supabaseUrl || !supabaseKey) {
        console.warn("Supabase 환경 변수가 설정되지 않았습니다. Mock 데이터를 사용합니다.");
        setMemes(mockMemes);
        setIsLoading(false);
        return;
      }

      const { data, error } = await supabase
        .from("memes")
        .select("*")
        .order("created_at", { ascending: false });

      if (error) {
        console.error("Error fetching memes:", error);
        console.error("Error details:", {
          message: error.message,
          details: error.details,
          hint: error.hint,
          code: error.code,
        });
        // Use mock data as fallback
        setMemes(mockMemes);
      } else if (data && data.length > 0) {
        // Convert database memes to Meme type
        const dbMemes: Meme[] = data.map((dbMeme) => ({
          id: dbMeme.id,
          imageUrl: dbMeme.image_url,
          title: dbMeme.title,
          episode: dbMeme.episode,
          quote: dbMeme.quote,
          category: dbMeme.category as Category,
          tags: dbMeme.tags as any[],
          likes: dbMeme.likes,
          isFavorite: false,
        }));
        setMemes([...dbMemes, ...mockMemes]);
      } else {
        // No data in database, use mock data
        setMemes(mockMemes);
      }
    } catch (error) {
      console.error("Error fetching memes:", error);
      if (error instanceof Error) {
        console.error("Error message:", error.message);
        console.error("Error stack:", error.stack);
      }
      setMemes(mockMemes);
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    fetchMemes();
  }, []);

  const filteredMemes = useMemo(() => {
    return memes.filter((meme) => {
      const matchesSearch =
        meme.title.toLowerCase().includes(searchQuery.toLowerCase()) ||
        meme.quote.toLowerCase().includes(searchQuery.toLowerCase()) ||
        meme.tags.some((tag) => tag.toLowerCase().includes(searchQuery.toLowerCase()));

      const matchesCategory =
        selectedCategory === "전체" || meme.category === selectedCategory;

      return matchesSearch && matchesCategory;
    });
  }, [memes, searchQuery, selectedCategory]);

  const handleLike = (id: string) => {
    setMemes((prev) =>
      prev.map((meme) =>
        meme.id === id
          ? {
              ...meme,
              isFavorite: !meme.isFavorite,
              likes: meme.isFavorite ? meme.likes - 1 : meme.likes + 1,
            }
          : meme
      )
    );
  };

  const handleMemeClick = (meme: Meme) => {
    setSelectedMeme(meme);
    setIsModalOpen(true);
  };

  const handleSignOut = async () => {
    const { error } = await supabase.auth.signOut();
    if (error) {
      toast.error("로그아웃 실패");
    } else {
      toast.success("로그아웃되었습니다");
    }
  };

  const handleUploadClick = () => {
    if (!user) {
      toast.error("로그인이 필요합니다");
      router.push("/auth");
      return;
    }
    setIsUploadOpen(true);
  };

  const handleEdit = (meme: Meme) => {
    toast.info("수정 기능은 곧 추가될 예정입니다");
    // TODO: 수정 다이얼로그 구현
  };

  const handleDelete = async (id: string) => {
    if (!isAdmin) {
      toast.error("관리자 권한이 필요합니다");
      return;
    }

    if (!confirm("정말 삭제하시겠습니까?")) {
      return;
    }

    try {
      const memeToDelete = memes.find(m => m.id === id);
      if (!memeToDelete) return;

      // Delete image from storage if it's from Supabase
      if (memeToDelete.imageUrl.includes("supabase")) {
        const urlParts = memeToDelete.imageUrl.split("/");
        const fileName = urlParts.slice(-2).join("/");
        await supabase.storage.from("meme-images").remove([fileName]);
      }

      // Delete from database
      const { error } = await supabase
        .from("memes")
        .delete()
        .eq("id", id);

      if (error) throw error;

      toast.success("삭제되었습니다");
      fetchMemes();
    } catch (error) {
      console.error("Error deleting meme:", error);
      toast.error("삭제 중 오류가 발생했습니다");
    }
  };

  return (
    <div className="min-h-screen bg-gradient-warm">
      {/* Header */}
      <header className="sticky top-0 z-50 bg-background/80 backdrop-blur-lg border-b border-border">
        <div className="container mx-auto px-4 py-4">
          <div className="flex items-center justify-between mb-6">
            <div className="flex items-center gap-3">
              <div className="p-2 bg-gradient-hero rounded-lg">
                <Image className="h-6 w-6 text-primary-foreground" />
              </div>
              <h1 className="text-3xl font-bold bg-gradient-hero bg-clip-text text-transparent">
                무도짤아카이브
              </h1>
            </div>
            
            <div className="flex items-center gap-2">
              {user ? (
                <>
                  {isAdmin && (
                    <Badge variant="default" className="gap-1">
                      <Shield className="h-3 w-3" />
                      관리자
                    </Badge>
                  )}
                  <Button onClick={handleUploadClick} className="gap-2">
                    <Upload className="h-4 w-4" />
                    짤 업로드
                  </Button>
                  <Button onClick={handleSignOut} variant="outline" className="gap-2">
                    <LogOut className="h-4 w-4" />
                    로그아웃
                  </Button>
                </>
              ) : (
                <Button onClick={() => router.push("/auth")} className="gap-2">
                  <LogIn className="h-4 w-4" />
                  로그인
                </Button>
              )}
            </div>
          </div>
          <SearchBar value={searchQuery} onChange={setSearchQuery} />
        </div>
      </header>

      {/* Category Filter */}
      <div className="container mx-auto px-4 py-6">
        <CategoryFilter selected={selectedCategory} onSelect={setSelectedCategory} />
      </div>

      {/* Meme Grid */}
      <main className="container mx-auto px-4 pb-12">
        {isLoading ? (
          <div className="text-center py-20">
            <p className="text-xl text-muted-foreground">로딩 중...</p>
          </div>
        ) : filteredMemes.length > 0 ? (
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
            {filteredMemes.map((meme) => (
              <MemeCard
                key={meme.id}
                meme={meme}
                onLike={handleLike}
                onClick={handleMemeClick}
                isAdmin={isAdmin}
                onEdit={handleEdit}
                onDelete={handleDelete}
              />
            ))}
          </div>
        ) : (
          <div className="text-center py-20">
            <p className="text-xl text-muted-foreground">검색 결과가 없습니다</p>
            <p className="text-sm text-muted-foreground mt-2">
              다른 키워드로 검색해보세요
            </p>
          </div>
        )}
      </main>

      {/* Meme Modal */}
      <MemeModal
        meme={selectedMeme}
        isOpen={isModalOpen}
        onClose={() => setIsModalOpen(false)}
        onLike={handleLike}
        isAdmin={isAdmin}
        onEdit={handleEdit}
        onDelete={handleDelete}
      />

      {/* Upload Dialog */}
      <UploadMemeDialog
        isOpen={isUploadOpen}
        onClose={() => setIsUploadOpen(false)}
        onSuccess={fetchMemes}
      />
    </div>
  );
}

