import { X, Heart, Share2, Download, Pencil, Trash2 } from "lucide-react";
import { Meme } from "@/types/meme";
import { Dialog, DialogContent } from "./ui/dialog";
import { Badge } from "./ui/badge";
import { Button } from "./ui/button";
import { toast } from "sonner";

interface MemeModalProps {
  meme: Meme | null;
  isOpen: boolean;
  onClose: () => void;
  onLike: (id: string) => void;
  isAdmin?: boolean;
  onEdit?: (meme: Meme) => void;
  onDelete?: (id: string) => void;
}

export const MemeModal = ({ meme, isOpen, onClose, onLike, isAdmin, onEdit, onDelete }: MemeModalProps) => {
  if (!meme) return null;

  const handleShare = () => {
    toast.success("링크가 클립보드에 복사되었습니다!");
  };

  const handleDownload = () => {
    toast.success("다운로드를 시작합니다!");
  };

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="max-w-4xl p-0 gap-0">
        <div className="grid md:grid-cols-2 gap-0">
          <div className="relative aspect-square bg-muted">
            <img
              src={meme.imageUrl}
              alt={meme.title}
              className="w-full h-full object-cover"
            />
          </div>

          <div className="p-6 space-y-6">
            <div className="space-y-2">
              <h2 className="text-2xl font-bold text-foreground">{meme.title}</h2>
              <p className="text-muted-foreground">{meme.episode}</p>
            </div>

            <div className="p-4 bg-muted rounded-lg">
              <p className="text-lg font-medium">&quot;{meme.quote}&quot;</p>
            </div>

            <div className="space-y-3">
              <div className="flex items-center gap-2">
                <span className="text-sm font-medium text-muted-foreground">카테고리:</span>
                <Badge variant="default">{meme.category}</Badge>
              </div>

              <div className="flex items-center gap-2 flex-wrap">
                <span className="text-sm font-medium text-muted-foreground">태그:</span>
                {meme.tags.map((tag) => (
                  <Badge key={tag} variant="secondary">
                    {tag}
                  </Badge>
                ))}
              </div>

              <div className="flex items-center gap-2">
                <Heart
                  className={`h-5 w-5 ${
                    meme.isFavorite ? "fill-accent text-accent" : "text-muted-foreground"
                  }`}
                />
                <span className="text-sm font-medium">{meme.likes.toLocaleString()} 좋아요</span>
              </div>
            </div>

            <div className="flex gap-2">
              <Button
                variant={meme.isFavorite ? "default" : "outline"}
                className="flex-1"
                onClick={() => onLike(meme.id)}
              >
                <Heart className="mr-2 h-4 w-4" />
                {meme.isFavorite ? "좋아요 취소" : "좋아요"}
              </Button>
              <Button variant="outline" onClick={handleShare}>
                <Share2 className="h-4 w-4" />
              </Button>
              <Button variant="outline" onClick={handleDownload}>
                <Download className="h-4 w-4" />
              </Button>
            </div>

            {isAdmin && (
              <div className="flex gap-2 pt-4 border-t">
                <Button
                  variant="outline"
                  className="flex-1"
                  onClick={() => {
                    onEdit?.(meme);
                    onClose();
                  }}
                >
                  <Pencil className="mr-2 h-4 w-4" />
                  수정
                </Button>
                <Button
                  variant="destructive"
                  className="flex-1"
                  onClick={() => {
                    onDelete?.(meme.id);
                    onClose();
                  }}
                >
                  <Trash2 className="mr-2 h-4 w-4" />
                  삭제
                </Button>
              </div>
            )}
          </div>
        </div>
      </DialogContent>
    </Dialog>
  );
};
