import { useState } from "react";
import { supabase } from "@/integrations/supabase/client";
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "./ui/dialog";
import { Button } from "./ui/button";
import { Input } from "./ui/input";
import { Label } from "./ui/label";
import { Textarea } from "./ui/textarea";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "./ui/select";
import { Badge } from "./ui/badge";
import { toast } from "sonner";
import { Upload, X } from "lucide-react";
import { Category, Emotion } from "@/types/meme";
import { z } from "zod";

const categories: Category[] = ["유재석", "박명수", "정형돈", "정준하", "하하", "노홍철", "길"];
const emotions: Emotion[] = ["웃김", "화남", "슬픔", "감동", "놀람", "당황", "사과", "현웃"];

const memeSchema = z.object({
  title: z.string().trim().min(1, "제목을 입력하세요").max(100, "제목은 100자 이내로 입력하세요"),
  episode: z.string().trim().min(1, "방송 회차를 입력하세요").max(100),
  quote: z.string().trim().min(1, "대사를 입력하세요").max(500, "대사는 500자 이내로 입력하세요"),
  category: z.string().min(1, "카테고리를 선택하세요"),
});

interface UploadMemeDialogProps {
  isOpen: boolean;
  onClose: () => void;
  onSuccess: () => void;
}

export const UploadMemeDialog = ({ isOpen, onClose, onSuccess }: UploadMemeDialogProps) => {
  const [isLoading, setIsLoading] = useState(false);
  const [imageFile, setImageFile] = useState<File | null>(null);
  const [imagePreview, setImagePreview] = useState<string>("");
  const [title, setTitle] = useState("");
  const [episode, setEpisode] = useState("");
  const [quote, setQuote] = useState("");
  const [category, setCategory] = useState<Category | "">("");
  const [selectedTags, setSelectedTags] = useState<Emotion[]>([]);

  const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      if (file.size > 10 * 1024 * 1024) {
        toast.error("이미지 크기는 10MB 이하여야 합니다");
        return;
      }
      if (!file.type.startsWith("image/")) {
        toast.error("이미지 파일만 업로드 가능합니다");
        return;
      }
      setImageFile(file);
      const reader = new FileReader();
      reader.onloadend = () => {
        setImagePreview(reader.result as string);
      };
      reader.readAsDataURL(file);
    }
  };

  const toggleTag = (tag: Emotion) => {
    setSelectedTags((prev) =>
      prev.includes(tag) ? prev.filter((t) => t !== tag) : [...prev, tag]
    );
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    try {
      // Validate form data
      const validated = memeSchema.parse({ title, episode, quote, category });

      if (!imageFile) {
        toast.error("이미지를 선택하세요");
        return;
      }

      if (selectedTags.length === 0) {
        toast.error("최소 1개의 태그를 선택하세요");
        return;
      }

      setIsLoading(true);

      // Get current user
      const { data: { user } } = await supabase.auth.getUser();
      if (!user) {
        toast.error("로그인이 필요합니다");
        return;
      }

      // Upload image to storage
      const fileExt = imageFile.name.split(".").pop();
      const fileName = `${user.id}/${Date.now()}.${fileExt}`;
      const { error: uploadError, data: uploadData } = await supabase.storage
        .from("meme-images")
        .upload(fileName, imageFile);

      if (uploadError) {
        toast.error("이미지 업로드 실패: " + uploadError.message);
        return;
      }

      // Get public URL
      const { data: { publicUrl } } = supabase.storage
        .from("meme-images")
        .getPublicUrl(fileName);

      // Insert meme data
      const { error: insertError } = await supabase.from("memes").insert({
        user_id: user.id,
        image_url: publicUrl,
        title: validated.title,
        episode: validated.episode,
        quote: validated.quote,
        category: validated.category,
        tags: selectedTags,
      });

      if (insertError) {
        toast.error("짤 등록 실패: " + insertError.message);
        return;
      }

      toast.success("짤이 성공적으로 등록되었습니다!");
      onSuccess();
      resetForm();
      onClose();
    } catch (error) {
      if (error instanceof z.ZodError) {
        toast.error(error.errors[0].message);
      } else {
        toast.error("짤 업로드 중 오류가 발생했습니다");
      }
    } finally {
      setIsLoading(false);
    }
  };

  const resetForm = () => {
    setImageFile(null);
    setImagePreview("");
    setTitle("");
    setEpisode("");
    setQuote("");
    setCategory("");
    setSelectedTags([]);
  };

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="max-w-2xl max-h-[90vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle>무도짤 업로드</DialogTitle>
        </DialogHeader>

        <form onSubmit={handleSubmit} className="space-y-4">
          {/* Image Upload */}
          <div className="space-y-2">
            <Label>이미지</Label>
            {imagePreview ? (
              <div className="relative">
                <img
                  src={imagePreview}
                  alt="Preview"
                  className="w-full h-64 object-cover rounded-lg"
                />
                <Button
                  type="button"
                  variant="destructive"
                  size="icon"
                  className="absolute top-2 right-2"
                  onClick={() => {
                    setImageFile(null);
                    setImagePreview("");
                  }}
                >
                  <X className="h-4 w-4" />
                </Button>
              </div>
            ) : (
              <label className="flex flex-col items-center justify-center w-full h-64 border-2 border-dashed rounded-lg cursor-pointer bg-muted hover:bg-muted/80 transition-smooth">
                <Upload className="h-12 w-12 text-muted-foreground mb-2" />
                <p className="text-sm text-muted-foreground">클릭하여 이미지 업로드</p>
                <input
                  type="file"
                  className="hidden"
                  accept="image/*"
                  onChange={handleImageChange}
                />
              </label>
            )}
          </div>

          {/* Title */}
          <div className="space-y-2">
            <Label htmlFor="title">제목</Label>
            <Input
              id="title"
              value={title}
              onChange={(e) => setTitle(e.target.value)}
              placeholder="예: 유재석 폭소"
              required
            />
          </div>

          {/* Episode */}
          <div className="space-y-2">
            <Label htmlFor="episode">방송 회차</Label>
            <Input
              id="episode"
              value={episode}
              onChange={(e) => setEpisode(e.target.value)}
              placeholder="예: 2012년 8월 25일 방송"
              required
            />
          </div>

          {/* Quote */}
          <div className="space-y-2">
            <Label htmlFor="quote">대사</Label>
            <Textarea
              id="quote"
              value={quote}
              onChange={(e) => setQuote(e.target.value)}
              placeholder="예: 하하하하하!"
              required
            />
          </div>

          {/* Category */}
          <div className="space-y-2">
            <Label>카테고리 (인물)</Label>
            <Select value={category} onValueChange={(value) => setCategory(value as Category)}>
              <SelectTrigger>
                <SelectValue placeholder="인물 선택" />
              </SelectTrigger>
              <SelectContent>
                {categories.map((cat) => (
                  <SelectItem key={cat} value={cat}>
                    {cat}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>

          {/* Tags */}
          <div className="space-y-2">
            <Label>태그 (감정/상황)</Label>
            <div className="flex flex-wrap gap-2">
              {emotions.map((emotion) => (
                <Badge
                  key={emotion}
                  variant={selectedTags.includes(emotion) ? "default" : "outline"}
                  className="cursor-pointer transition-smooth hover:scale-105"
                  onClick={() => toggleTag(emotion)}
                >
                  {emotion}
                </Badge>
              ))}
            </div>
          </div>

          <div className="flex gap-2 pt-4">
            <Button type="button" variant="outline" onClick={onClose} className="flex-1">
              취소
            </Button>
            <Button type="submit" disabled={isLoading} className="flex-1">
              {isLoading ? "업로드 중..." : "업로드"}
            </Button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );
};
